.checkout-page {
  background: #ffffff;
  min-height: 100vh;
  padding: 0px 100px;
}

.checkout-title {
  text-align: center;
  font-family: "Playfair Display", serif;
  font-size: 32px;
  margin-bottom: 40px;
}

/* EMPTY STATE */
.checkout-page.empty {
  text-align: center;
}

.checkout-page.empty h2 {
  font-family: "Playfair Display", serif;
  margin-bottom: 20px;
}

.back-btn {
  padding: 12px 28px;
  border: 1px solid #000;
  background: transparent;
  cursor: pointer;
}

/* WRAPPER */
.checkout-wrapper {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 40px;
  max-width: 1200px;
  margin: auto;
}

/* LEFT */
.checkout-items {
  background: #fff;
  padding: 30px;
}

.checkout-item {
  display: flex;
  align-items: center;
  gap: 20px;
  border-bottom: 1px solid #eee;
  padding: 20px 0;
}

.checkout-item:last-child {
  border-bottom: none;
}

.checkout-item img {
  width: 90px;
  height: 130px;
  object-fit: cover;
}

.checkout-info h4 {
  font-family: "Times New Roman", serif;
  margin-bottom: 6px;
}

.checkout-info p {
  font-size: 14px;
  color: #555;
}

.checkout-price {
  margin-left: auto;
  font-weight: 500;
}

/* RIGHT */
.checkout-summary {
  background: #fff;
  padding: 30px;
  height: fit-content;
}

.checkout-summary h3 {
  margin-bottom: 20px;
  font-family: "Playfair Display", serif;
}

.summary-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  font-size: 15px;
}

.summary-row.total {
  font-weight: bold;
  border-top: 1px solid #eee;
  padding-top: 12px;
  margin-top: 18px;
}

.place-order-btn {
  width: 100%;
  margin-top: 25px;
  padding: 14px 0;
  background: #000;
  color: #fff;
  border: none;
  cursor: pointer;
  letter-spacing: 1px;
}

.place-order-btn:hover {
  opacity: 0.9;
}


/* ===============================
   RESPONSIVE  CHECKOUT PAGE
   Desktop untouched
================================ */

/* ---------- TABLETS ---------- */
@media (max-width: 1024px) {

  .checkout-page {
    padding: 0 40px;
  }

  .checkout-wrapper {
    grid-template-columns: 1fr;   /*  stack layout */
    gap: 30px;
  }

  .checkout-summary {
    padding: 24px;
  }
}

/* ---------- MOBILE ---------- */
@media (max-width: 600px) {

  .checkout-page {
    padding: 0 16px;
  }

  .checkout-title {
    font-size: 24px;
    margin-bottom: 28px;
  }

  /* Checkout item layout */
  .checkout-item {
    align-items: flex-start;
    gap: 14px;
  }

  .checkout-item img {
    width: 70px;
    height: 100px;
  }

  .checkout-info h4 {
    font-size: 14px;
  }

  .checkout-info p {
    font-size: 13px;
  }

  .checkout-price {
    margin-left: 0;
    font-size: 14px;
    font-weight: 600;
  }

  /* Summary */
  .summary-row {
    font-size: 14px;
  }

  .place-order-btn {
    padding: 13px 0;
    font-size: 13px;
  }
}
