.about-section {
  background: #ffffff;
  padding: 15px 110px 35px;
  margin-bottom: 0; /*  REMOVE EXTRA GAP */
}

.about-inner {
  max-width: 1400px;
  margin: auto;
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  align-items: center;
  column-gap: 60px;
}

/* LEFT CONTENT */
.about-left {
  max-width: 560px;
  margin-top: -10px;
}

/* HEADINGS */
.about-label {
  font-family: "Playfair Display", serif;
  font-style: italic;
  font-size: 50px;
  letter-spacing: 6px;
  margin: 0 0 6px;
  color: #000;
  text-align: center;
}

.about-title {
  font-family: "Playfair Display", serif;
  font-size: 49px;
  font-style: italic;
  font-weight: 400;
  margin: 0 0 25px;
  text-align: center;
}

/* TEXT BLOCK */
.about-content {
  position: relative;
  max-width: 600px;
  margin-bottom: 0;
}

.about-text p,
.about-content p {
  font-family: "Times New Roman", serif;
  font-size: 15px;
  line-height: 2;
  letter-spacing: 1.3px;
  color: rgba(0,0,0,0.6);
  margin: 0;
  text-align: justify;
}

/* QUOTE MARK */
.about-content .quote {
  position: absolute;
  left: -40px;
  top: -14px;
  font-size: 58px;
  font-family: "Times New Roman", serif;
  color: #000;
  line-height: 1;
}

/* RIGHT IMAGE */
.about-right {
  display: flex;
  justify-content: flex-end;
}

.about-right img {
  width: 560px;
  height: auto;
  object-fit: cover;
}

/* ===============================
   RESPONSIVE  ABOUT SECTION
================================ */

/* Tablets */
@media (max-width: 1024px) {
  .about-section {
    padding: 30px 40px;
  }

  .about-inner {
    grid-template-columns: 1fr;
    row-gap: 50px;
  }

  .about-left {
    max-width: 100%;
    margin-top: 0;
    text-align: center;
  }

  .about-content {
    margin: 0 auto;
  }

  .about-content .quote {
    left: 50%;
    transform: translateX(-50%);
    top: -30px;
  }

  .about-right {
    justify-content: center;
  }

  .about-right img {
    width: 420px;
  }
}

/* Mobile */
@media (max-width: 600px) {
  .about-section {
    padding: 25px 18px 30px;
  }

  .about-label {
    font-size: 32px;
    letter-spacing: 3px;
  }

  .about-title {
    font-size: 30px;
  }

  .about-text p,
  .about-content p {
    font-size: 14px;
    line-height: 1.8;
    text-align: left;
  }

  .about-content .quote {
    font-size: 42px;
    top: -24px;
  }

  .about-right img {
    width: 100%;
    max-width: 320px;
  }
}
