.order-history-page {
  background:
    radial-gradient(circle at 10% 8%, rgba(201, 135, 61, 0.08), transparent 36%),
    linear-gradient(180deg, #fffaf4 0%, #f8f1e9 100%);
  padding: 50px 20px 80px;
}

.order-history-wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.order-history-title {
  font-family: "Cinzel", "Times New Roman", serif;
  font-size: clamp(1.8rem, 2.8vw, 2.4rem);
  letter-spacing: 1.4px;
  color: #7f102f;
  margin-bottom: 28px;
  text-align: center;
  text-transform: uppercase;
}

.order-history-empty {
  border: 1px dashed #ccbba9;
  border-radius: 16px;
  background: linear-gradient(180deg, #fffefb 0%, #fff7ef 100%);
  color: #5d4b41;
  font-size: 16px;
  text-align: center;
  padding: 30px 20px;
}

.order-history-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.order-history-card {
  position: relative;
  border: 1px solid #ffffff;
  border-radius: 18px;
  padding: 18px 20px;
  background: linear-gradient(180deg, rgba(255, 253, 248, 0.98) 0%, rgba(255, 249, 242, 0.98) 100%);
  box-shadow: 0 10px 24px rgba(50, 35, 26, 0.1);
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;
}

.order-history-card:hover {
  transform: translateY(-2px);
  border-color: rgba(127, 16, 47, 0.3);
  box-shadow: 0 14px 28px rgba(50, 35, 26, 0.15);
}

.order-history-header {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  align-items: flex-start;
  margin-bottom: 12px;
}

.order-history-id {
  color: #2d1f1a;
  font-size: 17px;
  font-family: "Cinzel", "Times New Roman", serif;
  letter-spacing: 0.4px;
  font-weight: 700;
}

.order-history-date {
  font-size: 13px;
  color: #6f5d50;
  margin-top: 3px;
}

.order-history-items {
  display: grid;
  gap: 10px;
  margin-bottom: 12px;
}

.order-history-item {
  display: grid;
  grid-template-columns: 76px 1fr;
  gap: 14px;
  align-items: center;
  border: 1px solid rgba(201, 135, 61, 0.2);
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.72);
  padding: 10px;
}

.order-history-item img {
  width: 76px;
  height: 96px;
  object-fit: cover;
  display: block;
  border-radius: 10px;
  border: 1px solid rgba(201, 135, 61, 0.28);
  box-shadow: 0 6px 12px rgba(45, 31, 26, 0.12);
}

.order-item-title {
  font-size: 17px;
  line-height: 1.25;
  color: #2d1f1a;
  margin-bottom: 5px;
}

.order-item-size {
  font-size: 13px;
  color: #6f5d50;
  margin-bottom: 4px;
}

.order-item-qty {
  font-size: 14px;
  color: #4f4138;
}

.order-history-summary {
  border-top: 1px solid rgba(201, 135, 61, 0.36);
  padding-top: 12px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 8px;
  font-size: 14px;
  color: #3f322b;
}

.order-history-summary div {
  border: 1px solid rgba(201, 135, 61, 0.24);
  border-radius: 10px;
  padding: 8px 10px;
  background: rgba(255, 255, 255, 0.7);
}

.order-summary-total {
  border-color: rgba(127, 16, 47, 0.3) !important;
  background: rgba(127, 16, 47, 0.08) !important;
  color: #7f102f;
  font-weight: 700;
}

@media (max-width: 600px) {
  .order-history-card {
    padding: 14px;
  }

  .order-history-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .order-item-title {
    font-size: 15px;
  }

  .order-item-qty {
    font-size: 13px;
  }

  .order-history-summary {
    grid-template-columns: 1fr;
  }
}
