
.product-page {
  max-width: 1200px;
  margin: 50px auto;
  display: flex;
  gap: 70px;
  padding: 0 20px; /* Required for mobile */
}

/* LEFT */
.product-gallery {
  display: flex;
  gap: 16px;
  width: 55%;
  align-items: flex-start;
}

.thumbs {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.thumbs img {
  width: 70px;
  height: 90px;
  object-fit: cover;
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 6px;
  opacity: 0.8;
  transition: opacity 0.2s ease, transform 0.2s ease, border-color 0.2s ease;
}

.thumbs img.active {
  border: 2px solid #8b0d2b;
  opacity: 1;
}

.thumbs img:hover {
  opacity: 1;
  transform: translateY(-2px);
}

.main-image {
  background: #f7f4f1;
  border: 1px solid #eee;
  border-radius: 12px;
  padding: 12px;
  overflow: hidden;
}

.main-image img {
  width: 700px;
  max-height: 700px;
  object-fit: cover;
  border-radius: 8px;
  transition: transform 0.45s ease;
}

.main-image:hover img {
  transform: scale(1.04);
}

/* RIGHT */
.product-info-panel {
  width: 45%;
}

.product-info-panel h1 {
  font-size: 28px;
  margin-bottom: 14px;
}

.product-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 6px 0 14px;
}

.meta-pill {
  border: 1px solid #ddd;
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 12px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #444;
  background: #fff;
}

.meta-pill.in {
  border-color: #2e7d32;
  color: #2e7d32;
  background: #f1fbf3;
}

.meta-pill.out {
  border-color: #c62828;
  color: #c62828;
  background: #fff2f2;
}

.meta-pill.sale {
  border-color: #8b0d2b;
  color: #8b0d2b;
  background: #fff5f7;
}

.price-row {
  display: flex;
  align-items: baseline;
  gap: 12px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.price {
  font-size: 30px;
  font-weight: 600;
}

.off {
  color: #2e7d32;
  font-size: 15px;
  margin-left: 6px;
}

.old {
  text-decoration: line-through;
  color: #999;
  font-size: 15px;
}

.savings {
  margin-top: -8px;
  margin-bottom: 16px;
  font-size: 14px;
  color: #8b0d2b;
}

/* DETAILS TABLE */
.details-box {
  border: 1px solid #ddd;
  margin-bottom: 25px;
}

.details-box h4 {
  padding: 12px;
  border-bottom: 1px solid #ddd;
}

.details-box td {
  padding: 12px;
  font-size: 14px;
}

.details-box td:first-child {
  width: 35%;
  color: #666;
}

/* ACTION ROW */
.action-row {
  display: flex;
  gap: 20px;
  align-items: center;
  margin-bottom: 18px;
}

.quantity-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  margin: 16px 0 18px;
  font-size: 13px;
  letter-spacing: 1px;
  color: #444;
}

.qty-box {
  display: flex;
  align-items: center;
  gap: 10px;
  border: none !important;
  outline: none !important;
}

.qty-box * {
  border: none !important;
  outline: none !important;
  box-shadow: none !important;
  appearance: none;
  -webkit-appearance: none;
  background: transparent;
  
}

.qty-box button {
  width: 32px;
  height: 32px;
  font-size: 18px;
  cursor: pointer;
}

.qty-box span {
  width: 32px;
  text-align: center;
  font-size: 14px;
}



.add-cart {
  width: 100%;
  max-width: 520px;
  height: 44px;
  border: 1px solid #ddd;
  background: #fff;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
  transition: background 0.25s ease, color 0.25s ease, border-color 0.25s ease;
}

.add-cart:hover {
  background: #111;
  color: #fff;
  border-color: #111;
}

.buy-now {
  width: 100%;
  padding: 16px;
  background: #8b0d2b;
  color: #fff;
  border: none;
  margin-bottom: 12px;
  cursor: pointer;
  letter-spacing: 1px;
  transition: background 0.25s ease, transform 0.2s ease;
}

.buy-now:hover {
  background: #6f0a22;
  transform: translateY(-1px);
}

.wishlist {
  position: relative;
  z-index: 999;
  pointer-events: auto;
}

/* ACCORDION */
.accordion {
  margin-top: 25px;
}

.accordion-item {
  display: flex;
  justify-content: space-between;
  padding: 14px 0;
  border-top: 1px solid #ddd;
  font-weight: 500;
}


/* ===============================
   WISHLIST POPUP
================================ */
/* WISHLIST BUTTON */
.wishlist {
  width: 100%;
  padding: 14px;
  border: 1px solid #ddd;
  background: #fff;
  color: #000;
  cursor: pointer;
  transition: all 0.3s ease;   /*  smooth hover */
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 13px;
}

/* HOVER EFFECT */
.wishlist:hover {
  background: #000;           /*  white  black */
  color: #fff;                /*  text white */
  border-color: #000;
}

/* ===============================
   PRODUCT PERKS + ACCORDION
================================ */

.product-perks {
  margin: 16px 0 6px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: #555;
  font-size: 15px;
}

.perk-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.perk-icon {
  font-size: 18px;
  color: #666;
}

.product-accordion {
  margin-top: 18px;
  border-top: 1px solid #d8d8d8;
}

.product-accordion-item {
  border-bottom: 1px solid #d8d8d8;
}

.product-accordion-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 0;
  background: none;
  border: none;
  cursor: pointer;
}

.product-accordion-header:hover .product-accordion-title {
  color: #8b0d2b;
}

.product-accordion-title {
  font-size: 15px;
  letter-spacing: 1px;
  font-weight: 600;
  color: #111;
}

.product-accordion-icon {
  font-size: 22px;
  line-height: 1;
  color: #333;
}

.product-accordion-body {
  padding: 0 0 16px 0;
  font-size: 14px;
  line-height: 1.8;
  color: #666;
}

/* WISHLIST OVERLAY */
.wishlist-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
}

/* MODAL BOX */
.wishlist-modal {
  background: #fff;
  padding: 40px 60px;
  max-width: 520px;
  width: 90%;
  text-align: center;
  position: relative;
  border-radius: 6px;
}

/* TITLE */
.wishlist-modal h2 {
  font-size: 18px;
  font-family: "Times New Roman", serif;
  letter-spacing: 1px;
  margin-bottom: 24px;
  color: #000;
}


.login-btn {
  background: #8b1538;
  color: #fff;
  border: none;
  padding: 12px 28px;
  font-size: 14px;
  cursor: pointer;
  border-radius: 4px;
}

.login-btn:hover {
  background: #6f102c;
}

.close-btn {
  position: absolute;
  top: 16px;
  right: 18px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #aaa;
}


/* ===============================
   ALSO BOUGHT
================================ */

.also-bought {
  max-width: 1200px;
  margin: 70px auto 90px;
  padding: 0 20px;
}

.also-bought-title {
  text-align: center;
  font-size: 18px;
  letter-spacing: 3px;
  margin-bottom: 32px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.also-bought-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 28px;
}

.also-card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 14px;
  overflow: hidden;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
}

.also-img {
  aspect-ratio: 3 / 4;
  background: #f7f4f1;
}

.also-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transition: transform 0.45s ease;
}

.also-info {
  padding: 14px 14px 16px;
}

.also-title {
  font-size: 14px;
  line-height: 1.35;
  margin-bottom: 8px;
  font-weight: 600;
  color: #1b1b1b;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  min-height: 36px;
}

.also-price {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 700;
  color: #111;
}

.also-price del {
  color: #999;
  font-weight: 500;
}

.also-cta {
  margin-top: 12px;
  width: 100%;
  padding: 9px 12px;
  border-radius: 999px;
  border: 1px solid #8b0d2b;
  background: transparent;
  color: #8b0d2b;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background 0.25s ease, color 0.25s ease, border-color 0.25s ease;
}

.also-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.08);
  border-color: #e4d7dc;
}

.also-card:hover img {
  transform: scale(1.04);
}

.also-card:hover .also-cta {
  background: #8b0d2b;
  color: #fff;
  border-color: #8b0d2b;
}

.product-info-panel,
.product-page {
  pointer-events: auto;
}


/* ===============================
   ACCORDION - AASHAKA STYLE
================================ */

.accordion {
  margin-top: 40px;
  border-top: 1px solid #d8d8d8;
  font-family: "Times New Roman", serif;
}

.accordion-item {
  border-bottom: 1px solid #d8d8d8;
}

/* HEADER */
.accordion-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 18px 0;
  cursor: pointer;
}

.accordion-title {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #656565;
}

.accordion-icon {
  font-size: 22px;
  font-weight: 300;
  line-height: 1;
  flex-shrink: 0;
  margin-left: auto;
}

/* BODY */
.accordion-body {
  position: relative;
  padding: 0 40px 22px 90px;   /*  key fix */
  font-size: 15px;
  line-height: 1.9;
  color: #555;
}


/* RIGHT SIDE VERTICAL LINE */
.accordion-body::after {
  content: "";
  position: absolute;
  right: 10px;
  top: 5px;
  height: calc(100% - 10px);
  width: 2px;
  background: #c7b6a4;
}

/* TEXT */
.accordion-body p {
  margin: 1%;
}


/* AASHAKA DESCRIPTION INNER CONTENT */

.description-body {
  max-width: 520px;           /* readable text column */
}
.desc-lead {
  font-size: 15.8px;
  line-height: 1.95;
  color: #444;
  margin-bottom: 18px;
}

.desc-sub {
  font-size: 15px;
  line-height: 1.85;
  color: #555;
}


/* SHIPPING LIST */
.accordion-body ul {
  margin: 0;
  padding-left: 18px;
}

.accordion-body li {
  margin-bottom: 12px;
}

.accordion-body b {
  font-weight: 600;
}


/* ===============================
   RESPONSIVE  PRODUCT PAGE
================================ */

/* ---------- TABLET ---------- */
@media (max-width: 1199px) {
  .product-page {
    flex-direction: column;
    gap: 40px;
    padding: 0 20px;
  }

  .product-gallery,
  .product-info-panel {
    width: 100%;
  }

  .product-gallery {
    flex-direction: column;
  }

  .thumbs {
    flex-direction: row;
    justify-content: center;
    gap: 12px;
  }

  .thumbs img {
    width: 60px;
    height: 80px;
  }

  .add-cart {
    width: 100%;
  }
}

/* ---------- MOBILE ---------- */
@media (max-width: 767px) {
  .product-page {
    margin: 30px auto;
    gap: 30px;
  }

  .main-image img {
    max-height: 480px;
  }

  .product-info-panel h1 {
    font-size: 22px;
  }

  .price {
    font-size: 18px;
  }

  .qty-box {
    justify-content: flex-start;
  }

  .buy-now,
  .wishlist,
  .add-cart {
    width: 100%;
  }
}


/* ===============================
   RESPONSIVE  ALSO BOUGHT
================================ */

@media (max-width: 1024px) {
  .also-bought-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media (max-width: 600px) {
  .also-bought-grid {
    grid-template-columns: 1fr;
    gap: 24px;
  }

  .also-bought-title {
    font-size: 15px;
    letter-spacing: 2px;
    margin-bottom: 30px;
  }
}



/* ===== FORCE MOBILE STACK ===== */
@media (max-width: 768px) {
  .product-page {
    flex-direction: column;
  }

  .product-gallery,
  .product-info-panel {
    width: 100% !important;
  }

  .product-gallery {
    flex-direction: column;
  }

  .thumbs {
    flex-direction: row;
    justify-content: center;
    margin-bottom: 12px;
  }

  .main-image img {
    width: 100%;
    max-height: 420px;
    object-fit: cover;
  }
}
  

/* ===============================
   FINAL RESPONSIVE PATCH  PRODUCT DETAIL
   Desktop untouched
================================ */

/* ---------- TABLETS ---------- */
@media (max-width: 1024px) {

  .main-image img {
    width: 100%;
    max-width: 100%;
    max-height: 520px;
  }

  .product-gallery {
    gap: 20px;
  }

  .product-info-panel {
    padding-top: 10px;
  }
}

/* ---------- MOBILE ---------- */
@media (max-width: 600px) {

  /* Layout spacing */
  .product-page {
    margin: 20px auto;
    gap: 24px;
  }

  /* Images */
  .main-image img {
    max-height: 380px;
  }

  .thumbs img {
    width: 52px;
    height: 68px;
  }

  /* Text */
  .product-info-panel h1 {
    font-size: 20px;
    line-height: 1.3;
  }

  .price {
    font-size: 17px;
  }

  /* Buttons  remove fixed widths safely */
  .add-cart {
    width: 100%;
    height: 42px;
  }

  .buy-now,
  .wishlist {
    padding: 14px;
  }

  /* Accordion */
  .accordion-body {
    padding: 0 0 18px 0;
    font-size: 14px;
  }

  .accordion-body::after {
    display: none; /* vertical line off on mobile */
  }
}

/* ===============================
   ALSO BOUGHT - MOBILE FIX
   Desktop untouched
================================ */

@media (max-width: 768px) {

  .also-bought {
    margin: 60px auto 80px;
    padding: 0 16px;
  }

  .also-bought-title {
    font-size: 16px;
    letter-spacing: 2px;
    margin-bottom: 24px;
  }

  .also-bought-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  .also-card {
    text-align: left;
  }

  .also-img {
    aspect-ratio: 1 / 1;
  }

  .also-info {
    padding: 12px;
  }

  .also-title {
    font-size: 13px;
    line-height: 1.3;
    margin-bottom: 6px;
  }

  .also-price {
    font-size: 13px;
  }

  .also-price del {
    font-size: 12px;
  }

  .also-cta {
    padding: 8px 10px;
    font-size: 11px;
  }

  /* Disable hover effects on touch */
  .also-card:hover,
  .also-card:hover img {
    transform: none;
    box-shadow: none;
  }
}


/* ===============================
   MOBILE FIX  THUMBNAILS BELOW MAIN IMAGE
   Desktop untouched
================================ */

@media (max-width: 768px) {

  .product-gallery {
    display: flex;
    flex-direction: column;
  }

  /* MAIN IMAGE FIRST */
  .main-image {
    order: 1;
  }

  /* THUMBNAILS BELOW */
  .thumbs {
    order: 2;
    margin-top: 12px;
    justify-content: center;
  }

  .thumbs img {
    width: 60px;
    height: 80px;
  }
}


/* ===============================
   SIZE SELECTION (KURTI PAGE)
================================ */

.size-section {
  margin: 18px 0 12px;
}

.size-title {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 10px;
}

/* size buttons row */
.size-options {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

/* individual size button */
.size-btn {
  width: 42px;
  height: 42px;
  padding: 0;
  border: 1.5px solid #bdbdbd;
  background: #fff;
  font-size: 13px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s ease;
}

/* hover (not active / not disabled) */
.size-btn:hover:not(.disabled):not(.active) {
  border-color: #8b0424;
  color: #8b0d2b;
}

/* keep active style on hover */
.size-btn.active:hover {
  background: #8b0d2b;
  color: #ffffff;
  border-color: #000000;
}

/* active / selected */
.size-btn.active {
  background: #8b0d2b;
  color: #ffffff;
  border-color: #000000;
}

/* out of stock */
.size-btn.disabled {
  border-color: #ddd;
  color: #aaa;
  cursor: not-allowed;
}

/* stock text */
.size-stock {
  margin-top: 6px;
  font-size: 12px;
  color: #666;
}

.size-help {
  margin-top: 8px;
  font-size: 12px;
  color: #666;
}

/* ===============================
   MOBILE TWEAKS
================================ */

@media (max-width: 768px) {
  .size-options {
    gap: 8px;
  }

  .size-btn {
    width: 38px;
    height: 38px;
    font-size: 12px;
  }
}


