/* MAIN BANNER */
.explore-collection {
  position: relative;
  width: 100%;
  height: 768px;
  overflow: hidden;
  background: #fff;
}

/* IMAGE */
.explore-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center top;
  background: #fff;

  display: block;
}

/* TEXT OVERLAY */
.explore-overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 80px;
  pointer-events: none;
}

/* HEADING */
.explore-overlay h2 {
  font-family: "Playfair Display", "Times New Roman", serif;
  font-size: 64px;
  letter-spacing: 4px;
  color: #d8b46c; /* gold tone */
  text-transform: uppercase;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .explore-collection {
    height: 480px;
  }

  .explore-overlay h2 {
    font-size: 44px;
  }
}

@media (max-width: 768px) {
  .explore-collection {
    height: 360px;
  }

  .explore-overlay h2 {
    font-size: 32px;
    letter-spacing: 2px;
  }
}


/* ===============================
   RESPONSIVE PATCH  EXPLORE
   Desktop untouched
================================ */

/* Small tablets & phones */
@media (max-width: 600px) {

  .explore-collection {
    height: 280px;
  }

  .explore-overlay {
    padding-top: 40px;
  }

  .explore-overlay h2 {
    font-size: 26px;
    letter-spacing: 1.5px;
    text-align: center;
  }
}

/* Very small phones */
@media (max-width: 420px) {

  .explore-collection {
    height: 250px;
  }

  .explore-overlay h2 {
    font-size: 22px;
  }
}
