/* ===============================
   NECKLACE PAGE  DESKTOP
================================ */

.necklace-section {
  background: #ffffff;
  padding: 35px 120px 100px;
}

.necklace-header {
  text-align: center;
  margin-bottom: 50px;
}

.necklace-header h1 {
  font-family: "Times New Roman", serif;
  font-size: 30px;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

.necklace-header p {
  font-size: 14px;
  color: #555;
}

/* ===============================
   GRID
================================ */

.necklace-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
}

/* ===============================
   PRODUCT CARD
================================ */

.product-card {
  background: #fff;
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 1px solid #eadfd6;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.08);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  cursor: pointer;
}

.product-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 30px rgba(0, 0, 0, 0.12);
}

.product-image img {
  width: 100%;
  aspect-ratio: 3 / 4;
  object-fit: cover;
  display: block;
  transition: transform 0.35s ease;
}

.product-image {
  overflow: hidden;
}

.product-card:hover .product-image img {
  transform: scale(1.05);
}

.product-info {
  padding: 14px 10px;
}

.product-title {
  font-family: "Times New Roman", serif;
  font-size: 15px;
  line-height: 1.35;
  margin-bottom: 6px;
  color: #222;
    min-height: 42px;          /*  KEY FIX */

}

.product-price {
  display: flex;
  justify-content: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 600;
}

.old-price {
  text-decoration: line-through;
  color: #999;
  font-size: 13px;
}

/* ===============================
   TABLET
================================ */

@media (max-width: 1024px) {

  .necklace-section {
    padding: 30px 40px 80px;
  }

  .necklace-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 26px;
  }

  .necklace-header h1 {
    font-size: 26px;
  }
}

/* ===============================
   MOBILE  2 PER ROW (NO SCROLL)
================================ */

@media (max-width: 600px) {

  .necklace-section {
    padding: 20px 16px 70px;
  }

  .necklace-grid {
    display: grid;                 /*  force grid */
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  .necklace-header {
    margin-bottom: 30px;
  }

  .necklace-header h1 {
    font-size: 22px;
    letter-spacing: 1.5px;
  }

  .necklace-header p {
    font-size: 13px;
  }

  .product-title {
    font-size: 13.5px;
  }

  .product-price {
    font-size: 13px;
  }

  .old-price {
    font-size: 12px;
  }
}
