.order-success {
  min-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  padding: 40px 20px;
}

.order-success-box {
  max-width: 620px;
  width: 100%;
  text-align: center;
  border: 1px solid #eee;
  padding: 50px 40px;
  border-radius: 6px;
  background: #fff;
}

.order-success-box h1 {
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 16px;
  color: #000;
}

.order-success-box p {
  font-size: 15px;
  color: #555;
  line-height: 1.6;
  margin-bottom: 10px;
}

.order-success-box .success-icon {
  font-size: 54px;
  color: #740318;
  margin-bottom: 20px;
}

.order-success-box a {
  display: inline-block;
  margin-top: 28px;
  padding: 14px 34px;
  border: 1px solid #000;
  text-decoration: none;
  color: #000;
  letter-spacing: 1.5px;
  font-size: 13px;
  transition: all 0.3s ease;
}

.order-success-box a:hover {
  background: #000;
  color: #fff;
}


/* ===============================
   RESPONSIVE  ORDER SUCCESS
   Desktop untouched
================================ */

/* Tablets */
@media (max-width: 768px) {

  .order-success-box {
    padding: 40px 30px;
  }

  .order-success-box h1 {
    font-size: 28px;
  }
}

/* Mobile phones */
@media (max-width: 480px) {

  .order-success {
    padding: 30px 16px;
  }

  .order-success-box {
    padding: 30px 20px;
  }

  .order-success-box h1 {
    font-size: 24px;
  }

  .order-success-box p {
    font-size: 14px;
  }

  .order-success-box .success-icon {
    font-size: 44px;
  }

  .order-success-box a {
    padding: 12px 26px;
    font-size: 12px;
    letter-spacing: 1.2px;
  }
}
