.placeorder-page {
  background: #ffffff;
  padding: 6px 0;
  font-family: "Times New Roman", serif;
}

.placeorder-wrapper {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: 1.3fr 0.7fr;
  gap: 50px;
}

  /* LEFT */
  .placeorder-left h2 {
    margin-bottom: 12px;
  }

  .placeorder-left input {
    width: 100%;
    padding: 16px;
    background: #e9ebe6;
    border: none;
    margin-bottom: 15px;  
    font-size: 14px;
  }

  .saved-address-select {
    width: 100%;
    padding: 14px 16px;
    border: 1px solid #e0d8cf;
    background: #fff9f4;
    font-size: 14px;
    letter-spacing: 0.6px;
    color: #3a2f2a;
    margin-bottom: 14px;
    appearance: none;
    background-image:
      linear-gradient(45deg, transparent 50%, #6b3a2a 50%),
      linear-gradient(135deg, #6b3a2a 50%, transparent 50%),
      linear-gradient(to right, #f0e4da, #f0e4da);
    background-position:
      calc(100% - 18px) 50%,
      calc(100% - 12px) 50%,
      100% 0;
    background-size: 6px 6px, 6px 6px, 44px 100%;
    background-repeat: no-repeat;
  }

  .saved-address-select:focus {
    outline: none;
    border-color: #8b0d2b;
    box-shadow: 0 0 0 2px rgba(139, 13, 43, 0.15);
  }

  .placeorder-left label {
  display: block;
  font-size: 14px;
  margin: 14px 0 6px;
  font-weight: 500;
}

.saved-addresses {
  margin: 8px 0 18px;
  padding: 14px 16px;
  border: 1px solid #eee;
  background: #fffdfb;
}

.saved-addresses-header {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  gap: 12px;
  margin-bottom: 12px;
  font-size: 13px;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: #6b3a2a;
}

.saved-addresses-hint {
  text-transform: none;
  letter-spacing: 0;
  font-size: 12px;
  color: #8a7d75;
}

.saved-addresses-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
}

.saved-address-card {
  display: grid;
  grid-template-columns: 1fr 18px;
  gap: 12px;
  padding: 12px 12px;
  border: 1px solid #eadfd6;
  background: #fff;
  cursor: pointer;
  position: relative;
  transition: border-color 0.2s ease, box-shadow 0.2s ease, transform 0.2s ease;
}

.saved-address-card:hover {
  border-color: #8b0d2b;
  box-shadow: 0 8px 16px rgba(139, 13, 43, 0.08);
  transform: translateY(-1px);
}

.saved-address-card input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.saved-address-content {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.saved-address-title {
  font-size: 14px;
  font-weight: 600;
  color: #2c2420;
}

.saved-address-line {
  font-size: 12.5px;
  color: #4b3f39;
}

.saved-address-line.muted {
  color: #7d6e66;
}

.saved-address-check {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: 1px solid #c9bdb5;
  margin-top: 4px;
  position: relative;
  justify-self: end;
}

.saved-address-card.selected {
  border-color: #8b0d2b;
  background: #fff7f8;
}

.saved-address-card.selected .saved-address-check {
  border-color: #8b0d2b;
}

.saved-address-card.selected .saved-address-check::after {
  content: "";
  position: absolute;
  inset: 3px;
  background: #8b0d2b;
  border-radius: 50%;
}

.error-text {
  color: red;
  font-size: 13px;
  margin-top: 4px;
}


.two-col {
  display: grid;
  grid-template-columns: 1fr; /* always single column */
  gap: 1px;
}

  .checkbox-row,
  .radio-row {  
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 16px 0;
    font-size: 14px;
    cursor: pointer;
    user-select: none; /* Prevents text highlighting on double click */
  }

  .checkbox-row input,
  .radio-row input {
    margin: 0;
    width: 18px;  /* Optional: makes them slightly easier to click */
    height: 18px;
    cursor: pointer;
  }

  .placeorder-btn {
    margin-top: 20px;
    padding: 14px 40px;
    border: 1px solid #000;
    background: white;
    font-size: 14px;
    letter-spacing: 2px;
    cursor: pointer;
  }

  .placeorder-btn :hover{
  color: white;
  background-color: #000;
  }

/* RIGHT */
.placeorder-right {
  background: #fff;
  padding: 30px;
  border: 1px solid #ddd;
}

.order-item {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.order-item img {
  width: 70px;
}

.summary {
  border-top: 1px solid #ddd;
  padding-top: 20px;
}

.summary div {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.summary .total {
  font-weight: bold;
  margin-top: 15px;
}


/* SECTION TITLE */
.section-title {
  font-size: 20px;
  font-family:monospace;
  margin: 40px 0 15px;
  font-weight: 600;
  margin-bottom: 20px;
}

/* PAYMENT BOX */
.payment-box {
  display: flex;
  gap: 40px;
  margin-top: 10px;
}

.radio-option {
  display: flex;
  align-items: center;
  gap: 14px;
  font-size: 15px;
  cursor: pointer;
}

.radio-option span {
  margin-left: 10px;
  align-items: center;
}

.radio-option input[type="radio"] {
  width: 16px;
  height: 16px;
  accent-color: #0026ff; /* matches Aashaka */
  cursor: pointer;
}


/* PLACE ORDER BUTTON */
.placeorder-btn {
  padding: 16px 55px;
  border: 1px solid #000;
  background: transparent;
  font-size: 14px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease;
  margin-bottom: 100px;
  margin-top: 0.5px;
}

/*  Hover effect */
.placeorder-btn:hover {
  background: #000;
  color: #fff;
}

/* tighten spacing between input + error */
.placeorder-left input {
  margin-bottom: 4px;
}

/* softer error */
.error {
  color: #b00020;
  font-size: 12px;
  margin: 2px 0 12px;
}

/* highlight input with error */
.placeorder-left input:has(+ .error) {
  border: 1px solid #b00020;
  background: #fff;
}


/* =====================================
   MOBILE CHECKOUT  AASHKA STYLE
===================================== */

@media (max-width: 768px) {

  /* Page padding */
  .placeorder-page {
    padding: 0;
  }

  /* Stack layout */
  .placeorder-wrapper {
    grid-template-columns: 1fr;
    gap: 24px;
    padding: 0 14px;
  }

  /* LEFT FORM */
  .placeorder-left {
    order: 1;
  }

  .placeorder-left h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .placeorder-left label {
    font-size: 13px;
    margin-top: 12px;
  }

  .placeorder-left input {
    padding: 14px;
    font-size: 14px;
    border-radius: 6px;
  }

  .saved-addresses {
    padding: 12px;
  }

  .saved-addresses-grid {
    grid-template-columns: 1fr;
  }

  /* Errors */
  .error-text {
    font-size: 12px;
    margin-bottom: 8px;
  }

  /* Payment */
  .section-title {
    font-size: 16px;
    margin: 26px 0 12px;
  }

  .payment-box {
    flex-direction: column;
    gap: 14px;
  }

  .radio-option {
    font-size: 14px;
  }

  /* Place order button */
  .placeorder-btn {
    width: 100%;
    padding: 16px;
    font-size: 14px;
    margin: 24px 0 40px;
  }

  /* RIGHT ORDER SUMMARY */
  .placeorder-right {
    order: 2;
    padding: 18px;
    border-radius: 6px;
    border: 1px solid #ddd;
    margin-bottom: 16px;
  }

  .placeorder-right h3 {
    font-size: 16px;
    margin-bottom: 14px;
  }

  .order-item {
    gap: 12px;
    margin-bottom: 16px;
  }

  .order-item img {
    width: 60px;
    border-radius: 4px;
  }

  .order-item p {
    font-size: 13px;
  }

  /* Summary */
  .summary div {
    font-size: 14px;
  }

  .summary .total {
    font-size: 15px;
  }
}

/* =====================================
   SMALL DEVICES (iPhone SE)
===================================== */
@media (max-width: 375px) {
  .placeorder-left input {
    padding: 12px;
    font-size: 13px;
  }

  .placeorder-btn {
    font-size: 13px;
  }
}

/* COUPON BOX */
.coupon-box {
  display: flex;
  gap: 10px;
  margin: 20px 0;
  order: -1;
}

.coupon-box input {
  flex: 1;
  padding: 14px;
  background: #e9ebe6;
  border: none;
}

.coupon-box button {
  padding: 14px 20px;
  border: 1px solid #000;
  background: #fff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.coupon-box button:hover {
  background: #000;
  color: #fff;
}

/* SUCCESS / ERROR STATES */
.coupon-box.success input {
  background: #e9f7ef;
}

.coupon-box.error input {
  background: #fdecea;
}

/* MESSAGE */
.coupon-message {
  font-size: 13px;
  margin-top: 6px;
  animation: fadeIn 0.4s ease;
}

.coupon-message.success {
  color: #2e7d32;
}

.coupon-message.error {
  color: #c62828;
}

/* DISCOUNT ANIMATION */
.animate-pop {
  animation: pop 0.4s ease;
}

@keyframes pop {
  0% {
    transform: scale(0.95);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* SUMMARY */
.discount-row {
  color: green;
  font-weight: 600;
}



/* ===============================
   MOBILE  COUPON FIX
================================ */

@media (max-width: 768px) {

  /* Ensure right section is visible */
  .placeorder-right {
    display: block;
  }

  /* Coupon must not collapse */
  .coupon-box {
    display: flex;
    width: 100%;
    margin: 16px 0 20px;
  }

  .coupon-box input {
    flex: 1;
    font-size: 14px;
  }

  .coupon-box button {
    white-space: nowrap;
    font-size: 13px;
    padding: 14px 16px;
  }

  /* Coupon message spacing */
  .coupon-message {
    margin-bottom: 10px;
  }
}
