.watch-modal {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* CARD */
.modal-card {
  position: relative;
  width: 380px;
  height: 680px;
  background: #000;
  border-radius: 18px;
  overflow: hidden;
  box-shadow: 0 30px 60px rgba(0,0,0,0.6);
}

/* VIDEO */
.modal-card video {
  width: 100%;
  height: calc(100% - 120px);
  object-fit: cover;
}

/* BOTTOM BAR */
.modal-bottom {
  position: absolute;
  bottom: 0;
  height: 120px;
  width: 100%;
  background: #fff;
  padding: 10px;
  text-align: center;
}

.modal-title {
  font-weight: 600;
  margin-bottom: 10px;
}

.buy-btn {
  width: 100%;
  padding: 14px;
  background: #333;
  color: #fff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

/* CLOSE */
.close {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 26px;
  color: #fff;
  cursor: pointer;
  z-index: 20;
}

/* MODAL ARROWS */
.nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(0,0,0,0.75);
  color: #fff;
  border: none;
  font-size: 26px;
  cursor: pointer;
  z-index: 20;
}

.nav.left {
  left: calc(50% - 240px);
}

.nav.right {
  right: calc(50% - 240px);
}

/* MOBILE */
@media (max-width: 480px) {
  .modal-card {
    width: 92%;
    height: 80vh;
  }

  .nav.left { left: 10px; }
  .nav.right { right: 10px; }
}
