.cart-page {
  background: #ffffff;
  padding: 6px 0;
  font-family: "Times New Roman", serif;
  margin-bottom: 80px;
}

.cart-wrapper {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 40px;
}

/* ================= LEFT ================= */

.cart-items {
  background: transparent;
}

.cart-item {
  display: grid;
  grid-template-columns: 32px 90px 1.8fr 0.9fr 1.2fr 0.9fr;
  gap: 20px;
  align-items: center;
  padding: 28px 0;
  border-bottom: 1px solid #ddd;
}

.cart-item img {
  width: 90px;
  height: auto;
  display: block;
  border-radius: 8px;
}

/* REMOVE */
.remove {
  border: none;
  background: none;
  font-size: 22px;
  cursor: pointer;
}

/* INFO */
.cart-info h4 {
  font-size: 16px;
  margin-bottom: 6px;
  font-weight: 400;
}

.cart-info .size {
  font-size: 13px;
  color: #666;
}

/* PRICE */
.cart-unit-price,
.cart-line-total {
  font-size: 15px;
  white-space: nowrap;
  text-align: right;
}

/* QUANTITY */
.qty-box.horizontal {
  display: flex;
  align-items: center;
  gap: 10px;
}

.qty-label {
  font-size: 12px;
  letter-spacing: 1px;
  color: #7a6f64;
  white-space: nowrap;
}

.qty-stepper {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 4px 6px;
  border: 1px solid #d7cfc6;
  border-radius: 999px;
  background: #f0efef;
}

.qty-stepper button {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 1px solid #c9beb3;
  background: #ffffff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.15s ease;
}

.qty-stepper button:hover {
  background: #efe6df;
}

.qty-value {
  min-width: 18px;
  text-align: center;
  font-size: 13px;
  font-weight: 600;
}

.qty-box button {
  width: 26px;
  height: 26px;
  border: 1px solid #000;
  background: transparent;
  cursor: pointer;
}

.stock-warning {
  font-size: 12px;
  color: #9b2c2c;
  margin-top: 6px;
  letter-spacing: 1px;
}


/* ================= RIGHT ================= */
/* ================= RIGHT : CART TOTAL ================= */

.cart-summary {
  background: #e8e8e7;
  padding: 48px 42px;
  font-family: "Times New Roman", serif;

  /*  THIS IS THE KEY */
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 110px;
  height: fit-content;
}

.summary-actions {
  margin-top: auto; /*  pushes buttons to bottom */
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.cart-summary h3 {
  font-size: 28px;
  font-weight: 400;
  margin-bottom: 36px;
}

/* rows */
.summary-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
  font-size: 17px;
  border-bottom: 1px solid #ddd;
}

/* labels */
.summary-row span:first-child {
  letter-spacing: 2px;
  font-size: 13px;
  color: #7a6f64;
}

/* TOTAL row */
.summary-row.total {
  border-bottom: none;
  padding-top: 22px;
  font-weight: 600;
}

.summary-row.total span:first-child {
  color: #000;
  letter-spacing: 2px;
}

/* divider (extra spacing already in image) */
.summary-divider {
  height: 0;
  margin: 0;
}

.shipping-note {
  margin-top: 16px;
  font-size: 13px;
  letter-spacing: 1px;
  color: #6b6057;
}

/* EMPTY STATE */
.empty-cart {
  background: #faf7f2;
  border: 1px solid #eadfd6;
  padding: 28px;
  text-align: center;
  border-radius: 14px;
}

.empty-cart h4 {
  font-size: 18px;
  margin: 8px 0 6px;
  font-weight: 500;
}

.empty-cart p {
  font-size: 14px;
  color: #666;
  margin-bottom: 16px;
}

.empty-icon {
  width: 56px;
  height: 56px;
  margin: 0 auto 8px;
  border-radius: 50%;
  background: #fff;
  border: 1px solid #eadfd6;
  display: grid;
  place-items: center;
  font-size: 12px;
  letter-spacing: 1px;
  color: #8b0d2b;
  text-transform: uppercase;
}

/* ================= BUTTON ================= */

.checkout-btn {
  margin-top: 42px;
  width: 100%;
  padding: 18px 0;
  border: 1px solid #000;
  font-size: 13px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 10px;
      background: transparent;
      color: #000;

}

.checkout-btn:hover {
  background-color: #000;
  color: #fff;
  transform: translateY(-1px);
}


/* RETURN TO SHOP */
.return-btn {
  width: 100%;
  padding: 18px 0;
  margin-bottom: 4px;
      background: transparent;


  border: 1px solid #000;

  font-size: 13px;
  letter-spacing: 2px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.return-btn:hover {
  background-color: #000;
  color: #ffffff;
}



/* ================= RESPONSIVE ================= */

/* TABLET */
@media (max-width: 1024px) {
  .cart-wrapper {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .cart-summary {
    position: static;
  }
}

/* MOBILE */
@media (max-width: 600px) {

  .cart-page {
    margin-bottom: 40px;
  }

  .cart-item {
    grid-template-columns: 28px 72px 1fr;
    row-gap: 12px;
  }

  .cart-unit-price,
  .cart-line-total,
  .qty-box.horizontal {
    grid-column: 3;
    text-align: left;
  }

  .remove {
    font-size: 20px;
  }

  .cart-summary {
    padding: 20px;
  }

  .summary-row {
    font-size: 14px;
  }

  .checkout-btn {
    padding: 12px;
    font-size: 13px;
  }
}
