.search-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
  padding: 12px;
  font-family: 'Courier New', Courier, monospace;
}

.search-title {
    text-align: center;
}

/* CARD */
.search-card {
  display: flex;
  flex-direction: column;
  font-family: "Times New Roman", serif;
  border: 1px solid #eadfd6;
  border-radius: 14px;
  overflow: hidden;
  background: #fff;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.08);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

/* IMAGE WRAPPER  THIS IS THE KEY */
.image-box {
  width: 100%;
  height: 360px;          /*  controls card height */
  overflow: hidden;
  background: #ffffff;
}

/* IMAGE ITSELF */
.image-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;     /*  magic line */
  display: block;
  transition: transform 0.35s ease;
}

.search-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 30px rgba(0, 0, 0, 0.12);
}

.search-card:hover .image-box img {
  transform: scale(1.05);
}

/* TITLE */
.product-title {
  margin-top: 2px;
  font-size: 16px;
  font-weight: 500;
  padding: 6px 12px 0;
}

/* PRICE */
.price {
  font-size: 15px;
  padding: 0 12px 14px;
}
