/* =========================
   SECTION BACKGROUND
========================= */

.category-section {
  background-color: #ffffff;
  padding: 6px 8px;
}

/* =========================
   CENTERED WRAPPER (DESKTOP)
========================= */

.category-wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

/* =========================
   GRID (DESKTOP)
========================= */

.category-grid {
  display: flex;
  justify-content: center;
  gap: 30px;
  padding-bottom: 60px;
}

/* =========================
   CARD
========================= */

.category-card {
  cursor: pointer;
  text-align: center;
  transition: transform 0.3s ease;
  flex-shrink: 0;
}

.category-card:hover {
  transform: translateY(-4px);
}

/* =========================
   IMAGE (CONSISTENT SIZE)
========================= */

.category-card img {
  width: 220px;
  height: 360px;              /* ?? FIXED HEIGHT = EVEN GRID */
  object-fit: cover;
  border-radius: 20px;
  display: block;
  transition: transform 0.4s ease;
}

.category-card:hover img {
  transform: scale(1.05);
}

/* =========================
   TEXT
========================= */

.category-card p {
  margin-top: 14px;
  font-family: "Times New Roman", serif;
  font-size: 15px;
  letter-spacing: 1px;
  color: #222;
}

/* ===============================
   MOBILE  HORIZONTAL CATEGORY SCROLL
   DESKTOP UNTOUCHED
================================ */

@media (max-width: 768px) {

  /* Break out of centered layout */
  .category-wrapper {
    max-width: 100%;
    margin: 0;
  }

  /* Horizontal scroll */
  .category-grid {
    display: flex;
    gap: 16px;
    overflow-x: auto;
    justify-content: flex-start;

    width: 100%;
    padding: 10px 16px 12px;
    margin: 0;

    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
  }

  /* Card sizing */
  .category-card {
    width: 160px;
    scroll-snap-align: start;
  }

  /* ?? FORCE SAME IMAGE SIZE ON MOBILE */
  .category-card img {
    width: 100%;
    height: 240px;           /* ?? CRITICAL FIX */
    object-fit: cover;
    border-radius: 20px;
  }

  /* Hide scrollbar */
  .category-grid::-webkit-scrollbar {
    display: none;
  }
}



/* ===============================
   CATEGORY ARROWS (ROUND)
   Layout untouched
================================ */

.category-wrapper {
  position: relative;
}

/* Arrow base */
.cat-arrow {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border: none;
  background: #ffffff;
  color: #222;
  font-size: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 5;
  box-shadow: 0 6px 18px rgba(0,0,0,0.18);
}

/* Arrow positions */
.cat-arrow.left {
  left: 6px;
}

.cat-arrow.right {
  right: 6px;
}



/* Mobile visibility */
@media (max-width: 768px) {
  .cat-arrow {
    display: flex;   /* always visible on mobile */
  }
}


/* ===============================
   ARROW VISIBILITY CONTROL
================================ */

/* Hide arrows on desktop */
.cat-arrow {
  display: none;
}

/* Show arrows only on mobile */
@media (max-width: 768px) {
  .cat-arrow {
    display: flex;
  }
}